//@flow
import React from "react";
import withStyles from "@material-ui/core/styles/withStyles";
//let webpack know that we want these fonts
import roboto300woff from "../fonts/roboto-latin-300.woff";
import roboto300woff2 from "../fonts/roboto-latin-300.woff2";
import roboto400woff from "../fonts/roboto-latin-400.woff";
import roboto400woff2 from "../fonts/roboto-latin-400.woff2";
import roboto500woff from "../fonts/roboto-latin-500.woff";
import roboto500woff2 from "../fonts/roboto-latin-500.woff2";

type Props = {||};
/*inspired by Mui's CssBaseline (previously named Reboot):
 https://github.com/mui-org/material-ui/blob/v1-beta/src/CssBaseline/CssBaseline.js
 it adds touch action and changes marging and padding rules
 */
const styles = theme => ({
  "@global": {
    "@font-face": [
      {
        fontFamily: "Roboto",
        fontStyle: "normal",
        fontDisplay: "swap",
        fontWeight: 300,
        src: `local("Roboto Light "), local("Roboto-Light"),
        url(${roboto300woff2}) format("woff2"),
        url(${roboto300woff}) format("woff")`
      },
      {
        fontFamily: "Roboto",
        fontStyle: "normal",
        fontDisplay: "swap",
        fontWeight: 400,
        src: `local("Roboto Regular "), local("Roboto-Regular"),
        url(${roboto400woff2}) format("woff2"),
        url(${roboto400woff}) format("woff")`
      },
      {
        fontFamily: "Roboto",
        fontStyle: "normal",
        fontDisplay: "swap",
        fontWeight: 500,
        src: `local("Roboto Medium "), local("Roboto-Medium"),
        url(${roboto500woff2}) format("woff2"),
        url(${roboto500woff}) format("woff")`
      }
    ],
    html: {
      // Antialiasing.
      WebkitFontSmoothing: "antialiased",
      MozOsxFontSmoothing: "grayscale",
      boxSizing: "border-box"
    },
    "*, *::before, *::after": {
      // width/height = border + padding +  content
      //remove default padding and margin
      boxSizing: "inherit",
      margin: 0,
      padding: 0
    },
    body: {
      //removes double-tap to zoom, allows  pan or pinch to zoom
      touchAction: "manipulation",
      fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
      overflow: "auto",
      backgroundColor: theme.palette.background.paper,
      "@media print": {
        // Save printer ink.
        backgroundColor: theme.palette.common.white
      }
    },
    /* hide chrome and ie11 clear button */
    'input[type="search"]::-webkit-search-cancel-button': {
      "-webkit-appearance": "none"
    },
    'input[type="search"]::-ms-clear': {
      display: "none"
    },
    /* hide ugly dotted outline in FF */
    "::-moz-focus-inner": {
      border: 0
    }
  }
});

class CssBase extends React.Component<Props> {
  shouldComponentUpdate() {
    return false;
  }
  render() {
    return null;
  }
}

export default withStyles(styles)(CssBase);



