//@flow
import React from "react";

export type AppState = {
  drawerOpen: boolean
};

export type Context = {
  state: AppState,
  updateState: (update: $Shape<Context>) => void
};

type State = {
  context: Context
};

const { Provider, Consumer } = React.createContext({ drawerOpen: false });

export const AppStateConsumer = Consumer;

export class AppStateProvider extends React.Component<{}, State> {
  updateState = (newState: $Shape<Context>) => {
    let res = { ...this.state.context.state, ...newState };

    this.setState({ context: { state: res, handlers: this.handlers } });
  };

  toggleDrawer = () => {
    this.updateState({ drawerOpen: !this.state.context.state.drawerOpen });
  };

  closeDrawer = () => {
    this.updateState({ drawerOpen: false });
  };

  openDrawer = () => {
    this.updateState({ drawerOpen: true });
  };

  handlers = {
    toggleDrawer: this.toggleDrawer,
    openDrawer: this.openDrawer,
    closeDrawer: this.closeDrawer
  };

  state = {
    context: {
      state: { drawerOpen: false },
      handlers: this.handlers
    }
  };

  render() {
    return (
      <Provider value={this.state.context}>{this.props.children}</Provider>
    );
  }
}



