//@flow
import * as React from "react";
import AppBar from "../components/AppBar";
import SideBar from "../components/SideBar";
import MenuButton from "../components/MenuButton";
import Helmet from "react-helmet";
import withRoot from "../containers/withRoot";
import { AppStateProvider } from "../containers/AppState";
import withStyles from "@material-ui/core/styles/withStyles";
import { APPBAR_MIN_HEIGHT, DRAWER_WIDTH } from "../config";
import Hidden from "@material-ui/core/Hidden";
import ToolBarGroup from "../components/ToolBarGroup";
import Typography from "@material-ui/core/Typography";
import NavMenu from "../components/NavMenu";

type Props = {
  children: () => React.Node,
  data: any,
  classes: MUIClasses
};

const classes = withStyles(theme => ({
  root: {
    display: "flex",
    zIndex: 1,
    backgroundColor: theme.palette.background.paper
  },
  heading: {
    alignSelf: "center"
  },
  main: {
    [theme.breakpoints.up("sm")]: {
      margin: `${APPBAR_MIN_HEIGHT + 16}px auto 0 ${DRAWER_WIDTH}px`
    },
    flex: 1,
    margin: `${APPBAR_MIN_HEIGHT + 16}px auto 0 auto`,
    display: "flex",
    justifyContent: "center"
  }
}));

class Layout extends React.Component<Props> {
  render() {
    const { classes, children, data } = this.props;

    return (
      <AppStateProvider>
        <div id="shell" className={classes.root}>
          <Helmet title={data.site.siteMetadata.title} />
          <AppBar>
            <ToolBarGroup>
              <Hidden smUp>
                <MenuButton />
              </Hidden>
              <Typography
                component="h1"
                variant="title"
                className={classes.heading}
              >
                {data.site.siteMetadata.title}
              </Typography>
            </ToolBarGroup>
          </AppBar>
          <SideBar>
            <NavMenu
              posts={data.allMarkdownRemark.edges.map(edge => ({
                path: edge.node.frontmatter.path,
                title: edge.node.frontmatter.title
              }))}
            />
          </SideBar>
          <main className={classes.main}>{children()}</main>
        </div>
      </AppStateProvider>
    );
  }
}

export default withRoot(classes(Layout));

/* eslint-disable-next-line no-undef */
export const query = graphql`
  query SiteTitleQuery {
    allMarkdownRemark {
      edges {
        node {
          frontmatter {
            path
            title
          }
        }
      }
    }
    site {
      siteMetadata {
        title
      }
    }
  }
`;



